import React from "react";
import '../styles/todo.css'
import useFormInput from '../customHooks/useFormInput';
import useList from '../customHooks/useList';

const AddTodo: React.FC = () => {
    let { input, valid, onChange, resetInput } = useFormInput('');
    let { todosList, addTodoHandler, toggleTodoHandler } = useList([]);

    const addTodo = () => {
        if (valid) {
            addTodoHandler(input)
            resetInput();
        }
        else return;
    }
    const renderList = () => {
        return todosList.map((todo) => < li key={todo.id} style={{ listStyleType: 'circle' }}>
            <span style={{ cursor: 'default' }} className={todo.complete ? "strike" : "undefined"} onClick={() => toggleTodoHandler(todo.id)}>{todo.todo} {todo.complete} </span>
        </li>
        )
    }
    return (
        <div style={{ margin: '10px 10px', transform: 'translate(25% ,20%)' }}>
            <input type='text' value={input} onChange={e => onChange(e.target.value)} />
            <button value="add" onClick={addTodo}> Add</button>
            {todosList.length !== 0 ?
                <ul>
                    {renderList()}
                </ul> : null}
        </div>
    )
}
export default AddTodo;